@import url('https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap');
*{
  padding: 0;
  margin: 0;
}
.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: peru;
}
.Menu{
  display: flex;
  justify-content: space-evenly;
  height: 100%;
  flex-direction: column;
  color: white;
  align-items: center;
  font-family: 'Permanent Marker', cursive;
}
.Menu ul{
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-direction: column;
  position: relative;
}
.Menu ul p{
  position: absolute;
  color: black;
  top: 40px;
  right: -30px;
}
.startBtn{
  padding: 30px 60px;
  background-color: white;
  border: 0;
  border-radius: 10px;
  cursor: pointer;
}.startBtn:hover{transform: scale(1.1);}


.Game{
  width: 40%;
  height: 80%;
  background: white;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.gameTime{
  width: 100%;
  height: 10%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.gameTime p:nth-child(2){
  position: absolute;
  top: 12px;
  right: 23px;
}
.gameTime p:nth-child(3){
  position: absolute;
  bottom: 7px;
  right: 12px;
}
.gameScene{
  width: 100%;
  height: 80%;
  display: grid;
  grid-template-columns: auto auto auto auto;
  align-content: space-evenly;
  justify-content: space-evenly;
}
.card{
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid rgba(0, 0, 0, 0.8);
  font-size: 30px;
  text-align: center;
  position: relative;
  transform: rotateY(180deg);
  transform-style: preserve-3d;
  transition: all 0.3s;
}
.card .front, .card .back{
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
}
.card-outer{
  border: 0;
  perspective: 1000px;
  width: 70px;
  height: 70px;
}
.card .front img{
  width: 100%;
  height: 100%;
}
.card .back{
  background-color: peru;
  transform: rotateY(180deg);
  display: flex;
  justify-content: center;
  align-items: center;
}
.card .back::after{
  content: '?' attr(href) '';
  cursor: pointer;
}
.card-outer.flipped .card{
  transform: rotateY(0deg);
}
.gameStop{
  width: 100%;
  height: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.gameStop button{
  padding: 15px 15px;
  background-color: black;
  border: 0;
  color: white;
  border-radius: 10px;
  cursor: pointer;
}.startBtn:hover{transform: scale(1.1);}



@media (max-width: 860px) {
  .Game{
    width: 80%;
    height: 80%;
    background: white;
  }
  .gameTime p:nth-child(2) {
    position: absolute;
    top: 50px;
    left: 50px;
  } 
  .gameTime p:nth-child(3) {
    position: absolute;
    top: 50px;
    right: 50px;
  }
}